<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
      status: "",
      title_show: false,
      isMobile: false
    };
  },
  computed: {
    stateYear() {
      return this.$store.state.status;
    }
  },
  watch: {
    stateYear: {
      handler(val) {
        this.$nextTick(() => {
          this.status = val;
        });
      },
      immediate: true
    }
  },
  methods: {
    go_route(pathstr) {
      if (this.status == "已登录") {
        console.log(pathstr);
        console.log(this.$router);
        this.$router.push({
          path: pathstr
        });
      } else {
        alert("请先登录");
      }
      // this.$router.push({
      //   path: pathstr
      // });
      this.title_show = false;
    },
    go_login(login) {
      this.$router.push({
        path: login
      });
      if (this.status == "退出登录") {
        // this.delCookie("sessionid");
        // this.delCookie("token");
        window.sessionStorage.clear();
        this.$router.push("/login");
        setTimeout(function() {
          window.location.reload();
        }, 600);
      }
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    show_title() {
      this.title_show = true;
    },
    close_title() {
      this.title_show = false;
    },
    show_self() {
      this.title_show = true;
    },
    close_self() {
      this.title_show = false;
    },
    delCookie(name) {
      //删除cookie函数
      var exp = new Date();
      exp.setTime(exp.getTime() - 1);
      var cval = this.getCookie(name);
      if (cval != null)
        document.cookie = name + "=" + cval + ";expires=" + exp.toGMTString();
    },
    getCookie(name) {
      //删除cookie函数
      var arr,
        reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
      if ((arr = document.cookie.match(reg))) return unescape(arr[2]);
      else return null;
    },
    changeTitle() {
      if (this.status == "已登录") {
        this.status = "退出登录";
      }
    },
    changeTitle2() {
      if (this.status == "退出登录") {
        this.status = "已登录";
      }
    }
  },
  created() {},
  mounted() {}
};
</script>
<style scoped>
.nav {
