<style>
.hbtn {
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  overflow: hidden;
  text-align: center;
  border: solid 2px;
  text-decoration: none;
  color: #ffffff;
  white-space: nowrap;
  z-index: 0;
  width: 100%;
  height: 100%;
  line-height: 27px;
  /* border-radius: 5px; */
  border-image: linear-gradient(#f80, #ff778d) 2 2 2;
  /* border-radius: 5px; */
  line-height: 30px;
}

.hbtn.hb-fill-middle2:before {
  position: absolute;
  content: "";
  /* background: #ffffff; */
  background-image: -webkit-gradient(
    linear,
    left top,
    right top,
    from(#ff778d),
    to(#ff5fa4)
  );
  transition-duration: 0.3s;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: auto;
  left: auto;
  width: 0;
  height: 100%;
  opacity: 1;
}

.hbtn.hb-fill-middle2:hover:before {
  width: 50%;
  height: 100%;
  opacity: 1;
}

.hb-fill-middle2 {
  background: transparent;
  color: #ffffff;
  transition: color 0.3s ease, background 0s ease;
}

.hb-fill-middle2:after {
  position: absolute;
  content: "";
  background-image: -webkit-gradient(
    linear,
    right top,
    left top,
    from(#ff778d),
    to(#ffa065)
  );
  transition-duration: 0.3s;
  z-index: -1;
  top: 0;
  right: auto;
  bottom: auto;
  left: 0;
  width: 0;
  height: 100%;
  opacity: 1;
}

.hb-fill-middle2:hover:after {
  width: 50%;
}

.LogOut {
  background: pink;
  font-size: 1rem;
  display: inline-block;
  border: none;
  width: 5.2vw;
  height: 4.3vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(50%, -50%);
  transition: all 1s;
  cursor: pointer;
  border-radius: 5px;
}

.LogOut:hover {
  background: Crimson;
}
</style>



