<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      activeIndex: "1",
      activeIndex2: "1",
      status: ""
    };
  },
  computed: {
    stateYear() {
      return this.$store.state.status;
    }
  },
  watch: {
    stateYear: {
      handler(val) {
        this.$nextTick(() => {
          this.status = val;
        });
      },
      immediate: true
    }
  },
  methods: {
    go_route(pathstr) {
      // if (this.status == "已登录") {
      //   console.log(pathstr);
      //   console.log(this.$router);
      //   this.$router.push({
      //     path: pathstr
      //   });
      // } else {
      //   alert("请先登录");
      // }
      this.$router.push({
        path: pathstr
      });
    },
    go_login(login) {
      this.$router.push({
        path: login
      });
    },
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  created() {},
  mounted() {}
};
</script>
<style scoped>
.nav {
  width: 100vw;
  height: 8vh;
  /* background-color: skyblue; */
  background-color: #01000235;
  display: flex;
  z-index: 10;
}
.logo_div {
  width: 200px;
  height: 8vh;
  margin-left: 200px;
}
.login_div {
  width: 200px;
  height: 8vh;
}
.login_a {
  display: inline-block;
  width: 100px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  /* color: #fc730a; */
  /* border: 1px solid #ddd; */
  text-align: center;
  margin-top: 2vh;
}
.login_a:hover {
  cursor: pointer;
}
/* .nav .nav_menu {
  padding-left: 40px;
} */

.el-menu.el-menu--horizontal {
  border-bottom: none;
}

.is-opened,
.is-active {
  background-color: none;
}
.el-menu--horizontal > .el-submenu .el-submenu__title {
  color: #fff !important;
}
</style>
<style>
/* 导航栏背景 */
.nav .el-menu--horizontal > .el-submenu .el-submenu__title {
  color: #fc730a !important;
  font-size: 0.9rem;
  font-family: Lantinghei SC;
  font-weight: 600;
}
/* 一级标题 */
.nav .el-menu--horizontal > .el-submenu .el-submenu__title:hover {
  color: #939099 !important;
  background-color: rgba(0, 0, 0, 0.5) !important;
}
/* 标题弹框 */
.el-menu {
  background: none;
}
/* 弹框的li */
.el-menu--horizontal .el-menu .el-menu-item,
.el-menu--horizontal .el-menu .el-submenu__title {
  background-color: rgba(255, 255, 255, 0.9) !important;
}
.el-menu--horizontal .el-menu .el-submenu__title:hover {
  color: #fc730a !important;
}
.el-menu-item:hover {
  color: #fc730a !important;
}
.hbtn {
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  overflow: hidden;
  text-align: center;
  border: solid 2px;
  text-decoration: none;
  color: #ffffff;
  white-space: nowrap;
  z-index: 0;
  width: 100%;
  height: 100%;
  line-height: 27px;
  /* border-radius: 5px; */
  border-image: linear-gradient(#f80, #ff778d) 2 2 2;
  /* border-radius: 5px; */
  line-height: 30px;
}

.hbtn.hb-fill-middle2:before {
  position: absolute;
  content: "";
  /* background: #ffffff; */
  background-image: -webkit-gradient(
    linear,
    left top,
    right top,
    from(#ff778d),
    to(#ff5fa4)
  );
  transition-duration: 0.3s;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: auto;
  left: auto;
  width: 0;
  height: 100%;
  opacity: 1;
}

.hbtn.hb-fill-middle2:hover:before {
  width: 50%;
  height: 100%;
  opacity: 1;
}

.hb-fill-middle2 {
  background: transparent;
  color: #ffffff;
  transition: color 0.3s ease, background 0s ease;
}

.hb-fill-middle2:after {
  position: absolute;
  content: "";
  background-image: -webkit-gradient(
    linear,
    right top,
    left top,
    from(#ff778d),
    to(#ffa065)
  );
  transition-duration: 0.3s;
  z-index: -1;
  top: 0;
  right: auto;
  bottom: auto;
  left: 0;
  width: 0;
  height: 100%;
  opacity: 1;
}

.hb-fill-middle2:hover:after {
  width: 50%;
}
</style>



