import Vue from "vue";
import ElementUI, { Message, Loading } from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
// 导入umy-ui
import UmyUi from "umy-ui";
import "umy-ui/lib/theme-chalk/index.css"; // 引入样式
import App from "./App";
import router from "./router";
import * as api from "./network/api";
import * as api2 from "./network/api2";
import * as api3 from "./network/api3";
import * as utils from "./utils/utils";
import VideoPlayer from "vue-video-player";
// use umy-ui
Vue.use(UmyUi);
require("video.js/dist/video-js.css");
require("vue-video-player/src/custom-theme.css");

import AFTableColumn from "af-table-column";
Vue.use(AFTableColumn);
// import Plugin from 'v-fit-columns';
// Vue.use(Plugin);

// const fontRate = {
//     CHAR_RATE: 1.1, // 汉字比率
//     NUM_RATE: 0.65, // 数字
//     OTHER_RATE: 0.8 // 除汉字和数字以外的字符的比率
// }
// const fontSize = 16
//     // 注册组件
// Vue.use(AFTableColumn, { fontRate, fontSize })

Vue.use(VideoPlayer);
// import vueCanvasNest from 'vue-canvas-nest'
Vue.prototype.$utils = utils;

// import VueParticles from 'vue-particles'

// Vue.use(VueParticles)

// 引入echart
import echarts from "echarts";
Vue.prototype.$echarts = echarts;

//全局挂载store
import store from "./store/store";

// 修改测试：
import axios from "axios";
Vue.prototype.$axios = axios;
import { commonUrl, commonUrl1 } from "utils/common";
// 修改全局message的显示时间

//后端获取cooke
axios.defaults.withCredentials = true;

Vue.config.productionTip = false;
Vue.use(ElementUI);
Vue.prototype.$api = api;
Vue.prototype.$api2 = api2;
Vue.prototype.$api3 = api3;
Vue.prototype.$axios = axios;

// 打包不需要改这里，去utils文件的common那里改 打包只需要改common与config
Vue.prototype._url2 = commonUrl;
Vue.prototype._urlzgl = commonUrl;
Vue.prototype._url3 = commonUrl1;
Vue.prototype._url4 = "https://operate.beyoung.group/";

/* eslint-disable no-new */
/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  store,
  render: h => h(App)
});

axios.interceptors.request.use(config => {
  config.headers.Authorization = encodeURIComponent(
    window.sessionStorage.getItem("user_name") +
      "@" +
      window.sessionStorage.getItem("area") +
      "@" +
      window.sessionStorage.getItem("PERMISSION_SESSION_KEY")
  );
  return config;
});



