import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);

const state = {
  //要设置的全局访问的state对象
  status: "登录",
  // 屏幕宽度
  clientWidth: "",
  // 店铺信息
  PI_Query: {
    condition: "",
    page: ""
  }
};

const store = new Vuex.Store({
  state,
  mutations: {
    getClientWidth(state, payload) {
      state.clientWidth = payload;
    },
    getIpQuery(state, payload) {
      state.PI_Query = payload;
    }
  }
});

export default store;



