import instance from "./request";
/*
 * ---------------人员资料相关接口开始------------------
 */

// 获取员工列表
export function getPersonList() {
  return instance.get("/server/personnel_management/personnel_data/");
}
// 新增员工
export function addPersonInfo(formData) {
  return instance.post("/server/personnel_management/personnel_add/", formData);
}

// 编辑员工
export function editPerson(formData) {
  return instance.post(
    "/server/personnel_management/personnel_edit/",
    formData
  );
}

// 删除员工
export function removePerson(id) {
  return instance.get("/server/personnel_management/personnel_del/", {
    params: {
      personnel_id: id
    }
  });
}

/*
 *  ---------------培训接口开始------------------
 */

// 获取培训记录列表
export function getTrainList() {
  return instance.get("/server/personnel_management/train/");
}

// 培训任务新增
// 所需字段：train_date：培训时间 train_theme：培训主题 train_file：培训文件（有多个文件传字符串，例：a.jpg,b.jpg） train_user：培训人员（有多个文件传字符串，例：小a,小b） train_remarks：备注说明
export function addTrainTask(formData) {
  return instance.post(
    "/server/personnel_management/train_work_add/",
    formData
  );
}

// 培训文档新增
export function addTrainFile(formData) {
  return instance.post(
    "/server/personnel_management/train_file_add/",
    formData
  );
}

// 培训文档汇总
export function gatherTrainFile() {
  return instance.get("/server/personnel_management/train_file_view/");
}

// 培训文档删除
export function removeTrainFile(train_theme, train_file) {
  return instance.get("/server/personnel_management/train_file_del/", {
    params: {
      train_theme,
      train_file
    }
  });
}

// 培训评分
export function trainScore(train_user, train_theme, score) {
  return instance.get("/server/personnel_management/train_score/", {
    params: {
      train_user,
      train_theme,
      score
    }
  });
}

/*
 *  ---------------工单相关接口开始------------------
 */

/*
 *  ---------------已提交工单接口开始------------------
 */

// 获取下拉框数据  参数部门，接单人
export function getSelectData(receive_department, receive_person) {
  return instance.get("/server/personnel_management/get_select_job/", {
    params: {
      receive_department,
      receive_person
    }
  });
}

// 点击查询，查看符合条件的工单数据，刚打开页面时，页面加载完时触发该请求，点查询时触发该请求 参数可为空但页数必须有
export function queryInfo(info) {
  return instance.get("/server/personnel_management/select_initiate_job/", {
    params: {
      receive_department: info.checkedDpm,
      receive_person: info.checkedStaff,
      job_type: info.checkedType,
      out_time: info.checkedDate,
      page: info.page
    }
  });
}

// 确认提交工单 formdata包含参数receive_department，receive_person，job_type，out_time，need
export function orderSubmit(formData) {
  return instance.post(
    "/server/personnel_management/insert_initiate_job/",
    formData
  );
}

// 点击编辑获取信息
export function getOrderInfo(job_id) {
  return instance.get("/server/personnel_management/get_update_initiate_job/", {
    params: {
      job_id
    }
  });
}

// 编辑弹窗点击确认 fromData所需数据job_id，out_time，need，next_info
export function editOrder(formData) {
  return instance.post(
    "/server/personnel_management/update_initiate_job/",
    formData
  );
}

// 删除工单
export function removeOrder(job_id) {
  return instance({
    url: "/server/personnel_management/delete_initiate_job/",
    method: "post",
    params: {
      job_id
    }
  });
}

// 结单确认 只能选择一个
// export function jiedanConfirm(job_id) {
//   return instance.post('/server/system_management/sure_initiate_job/', job_id)
// }
export function jiedanConfirm(job_id) {
  return instance({
    url: "/server/personnel_management/sure_initiate_job/",
    method: "post",
    params: {
      job_id
    }
  });
}
// 截单驳回
// export function jiedanReject(job_id) {
//   return instance.post('/server/system_management/refuse_initiate_job/', job_id)
// }
export function jiedanReject(job_id) {
  return instance({
    url: "/server/personnel_management/refuse_initiate_job/",
    method: "post",
    params: {
      job_id
    }
  });
}

/*
 *  ---------------已接工单接口开始------------------
 */

//查询已接工单 参数initiate_department，initiate_person，job_type，out_time，page
export function queryAccepted(info) {
  return instance.get("/server/personnel_management/select_receive_job/", {
    params: {
      initiate_department: info.checkedDpm,
      initiate_person: info.checkedStaff,
      job_type: info.checkedType,
      out_time: info.checkedDate,
      page: info.page
    }
  });
}

export function jiedan(job_id) {
  return instance({
    url: "/server/personnel_management/update_receive_job/",
    method: "post",
    params: {
      job_id
    }
  });
}

/*
 *  ---------------待处理工单接口开始------------------
 */
// 获取未处理工单列表
export function getWaitOrder(page) {
  return instance.get("/server/personnel_management/select_wait_job/", {
    params: {
      page
    }
  });
}

// 接取工单触发
export function acceptOrder(job_id) {
  return instance({
    url: "/server/personnel_management/receive_wait_job/",
    method: "post",
    params: {
      job_id
    }
  });
}

// 转派
export function transferOrder(info) {
  return instance({
    url: "/server/personnel_management/turn_wait_job/",
    method: "post",
    params: {
      job_id: info.transferId,
      receive_department: info.transferDpm,
      receive_person: info.transferStaff
    }
  });
}

// 驳回
export function rejectOrder(info) {
  return instance({
    url: "/server/personnel_management/refuse_wait_job/",
    method: "post",
    params: {
      job_id: info.rejectId,
      refuse_reason: info.rejectReason
    }
  });
}

/*
 *  ---------------绩效接口开始------------------
 */

// 首页查看绩效
export function visitAch() {
  return instance.get("/server/personnel_management/performance/");
}

// 绩效评定获取数据
export function visitAchScore() {
  return instance.get("/server/personnel_management/staff_performance/");
}

