// 同步对照组
export function getTableData(params) {
  return instance.get("/server/order_management/upload_auto/", {
    params
  });
}
// 增加
export function AddSubmit(params) {
  return instance.post("/server/order_management/upload_auto/", params);
}
// 编辑
export function editFormData(params) {
  return instance.put("/server/order_management/upload_auto/", params);
}

//delete 请求同get请求
