export function AMeditFormData(params) {
  return instance.put(
    "/server/order_management/Upload_transform_auto/",
    params
  );
}

//delete 请求同get请求
// 删除
export function AMdeleteFormData(params) {
  return instance.delete("/server/order_management/Upload_transform_auto/", {
    params
  });
}



