// 获取页面条码数据
export function codeQueryInfo(info) {
  return instance.get('/server/databases/select_barcode/', {
    params: {
      platform: info.checkedType,
      site: info.checkedSite,
      country: info.checkedCountry,
      sku: info.checkedSku,
      page: info.page
    }
  })
}
