<template>
  <div id="app" @scroll="handleScroll">
    <!-- <vue-canvas-nest :config="{color:'255,255,255', count: 500}" :el="'#app'" ></vue-canvas-nest> -->
    <Nav1 v-if="!isMobile"></Nav1>
    <phone-nav v-else></phone-nav>
    <div v-if="isPlayer">
      <video-player
        class="video-player vjs-custom-skin"
        ref="videoPlayer"
        :playsinline="true"
        :options="playerOptions"
        style="object-fit:fill"
        x5-video-player-fullscreen="true"
      ></video-player>
    </div>

    <router-view />
  </div>
</template>

<script>
import Nav from "@/common/Nav";
import Nav1 from "@/common/new_nav";
import vueCanvasNest from "vue-canvas-nest";
import PhoneNav from "@/common/PhoneNav";
export default {
  data() {
    return {
      str: "",
      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
        autoplay: true, //如果true,浏览器准备好时开始回放。
        muted: true, // 默认情况下将会消除任何音频。
        loop: true, // 导致视频一结束就重新开始。
        preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: "zh-CN",
        // aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [
          {
            type: "",
            src: "" //url地址
            // src: 'http://www.html5videoplayer.net/videos/madagascar3.mp4'//url地址
            // src: "" //url地址
          }
        ],
        poster: "", //你的封面地址
        // width: document.documentElement.clientWidth,
        notSupportedMessage: "此视频暂无法播放，请稍后再试", //允许覆盖Video.js无法播放媒体源时显示的默认信息。
        controlBar: {
          timeDivider: false,
          durationDisplay: false,
          remainingTimeDisplay: false,
          fullscreenToggle: true //全屏按钮
        }
      },
      isPlayer: true
    };
  },
  name: "App",
  methods: {
    // getList(){
    //   this.$api2.getList(a).then(res => {
    //     console.log(res);
    //   })
    // },
    handleScroll(e) {
      if (document.querySelector(".leftNav")) {
        if (document.querySelector("#app").scrollTop > 75) {
          document.querySelector(".leftNav").style.top = 0;
        } else {
          document.querySelector(".leftNav").style.top =
            75 - document.querySelector("#app").scrollTop + "px";
        }
      }
    },
    /**
     * @description: 判断是否是移动端
     * @param {*}
     * @return {*}
     */
    isMobile() {
      let flag = navigator.userAgent.match(
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
      );
      return flag;
    },
    /**
     * @description: 屏幕变化是否显示视频
     * @param {*}
     * @return {*}
     */
    isShowPlayer() {
      console.log(document.body.clientWidth);
      let val = document.body.clientWidth;
      this.$store.commit("getClientWidth", val);
      const def = 992; // 默认宽度
      if (val < def) {
        this.isPlayer = false;
      } else {
        this.isPlayer = true;
      }
    },
    /**
     * @description: 初始化时是否显示视频
     * @param {*}
     * @return {*}
     */
    initShowPlayer() {
      let val = document.body.clientWidth;
      if (val < 992) {
        this.isPlayer = false;
      } else {
        this.isPlayer = true;
      }
    }
  },
  computed: {},
  mounted() {
    this.initShowPlayer();
    window.addEventListener("scroll", this.handleScroll);
    this.$refs.videoPlayer.player.play();
    let user_name = sessionStorage.getItem("user_name");
    if (user_name) {
      this.$store.state.status = "已登录";
    } else {
      this.$store.state.status = "登录";
    }

    if (this.isMobile()) {
      console.log("是手机");

      this.isMobile = true;
    } else {
      console.log("不是手机");

      this.isMobile = false;
    }
    window.onresize = this.$utils.throttle(() => {
      this.isShowPlayer();
    }, 600);
  },
  created() {
    let date = new Date(); //获取当前年月
    let week = date.getDay();
    if (week == 1) {
      this.str = "../static/images/Monday.mp4";
    } else if (week == 2) {
      this.str = "../static/images/Tuesday.mp4";
    } else if (week == 3) {
      this.str = "../static/images/Wednesday.mp4";
    } else if (week == 4) {
      this.str = "../static/images/Thursday.mp4";
    } else if (week == 5) {
      this.str = "../static/images/Friday.mp4";
    } else {
      this.str = "../static/images/Weekend.mp4";
    }
    this.playerOptions.sources[0].src = this.str;
  },
  components: {
    Nav,
    Nav1,
    PhoneNav
    // vueCanvasNest
  }
};
</script>

<style>
@media screen and (min-width: 1400px) {
  html {
    font-size: 19px;
  }
}

@media screen and (min-width: 1024px) and (max-width: 1199px) {
  html {
    font-size: 16px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  html {
    font-size: 12px;
  }
}

@media screen and (max-width: 768px) {
  html {
    font-size: 10px;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #000;
}

ul li {
  list-style: none;
}

input,
textarea {
  outline: none;
}

body {
  font-family: "Microsoft YaHei";
  color: #333;
}

img {
  border: none;
  vertical-align: middle;
}
#app {
  /* overflow-x: hidden; */
  height: 100vh;
  /* overflow-y:auto; */
  overflow-y: auto;
}
table {
  background: rgba(255, 255, 255, 0.9) !important;
}
.leftNav {
  background: rgba(255, 255, 255, 0.9) !important;
}
input[type="file"] {
  display: inline-block;
}
.cash {
  position: absolute;
  top: 8vh;
  left: 0;
  width: 100vw;
  height: 92vh;
  /* z-index: 99; */
}

.video-player {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -15;
}
.el-table td,
.el-table th {
  padding: 0;
}
.el-table .cell {
  padding-right: 0;
}

/* 修改人员管理样式，还原el-table */
.el-select-dropdown__item {
  padding: 0 20px !important;
}
</style>



