<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      // 登录状态
      status: "",
      // 抽屉相关
      drawerVisible: false
    };
  },
  computed: {
    stateYear() {
      return this.$store.state.status;
    }
  },
  watch: {
    stateYear: {
      handler(val) {
        this.$nextTick(() => {
          this.status = val;
        });
      },
      immediate: true
    }
  },
  methods: {
    go_login(login) {
      this.$router.push({
        path: login
      });
      if (this.status == "退出登录") {
        // this.delCookie("sessionid");
        // this.delCookie("token");
        window.sessionStorage.clear();
        this.$router.push("/login");
        setTimeout(function() {
          window.location.reload();
        }, 600);
      }
    },
    changeTitle() {
      if (this.status == "已登录") {
        this.status = "退出登录";
      }
    },
    changeTitle2() {
      if (this.status == "退出登录") {
        this.status = "已登录";
      }
    },
    /**
     * @description: 展示抽屉导航
     * @param {*}
     * @return {*}
     */
    showDrawer() {
      this.drawerVisible = true;
    },
    // 跳转页面
    go_route(pathstr) {
      if (this.status == "已登录") {
        console.log(pathstr);
        console.log(this.$router);
        this.$router.push({
          path: pathstr
        });
        this.drawerVisible = false;
      } else {
        alert("请先登录");
        this.drawerVisible = false;
      }
      // this.$router.push({
      //   path: pathstr
      // });
      this.title_show = false;
    }
  },
  created() {},
  mounted() {}
};
</script>
<style scoped>
#phone-nav {
  height: 8vh;
}
.phone {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 8vh;
  background-color: #080808;
  display: flex;
  align-items: center;
  justify-content: space-around;
  z-index: 99999;
}
.logo {
  width: 42vw;
  height: 100%;
}
.logo img {
  width: 100%;
}
.el-icon-s-unfold {
  font-size: 5rem;
  color: #fff;
}
/* 登录按钮相关 */
.hbtn {
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  overflow: hidden;
  text-align: center;
  border: solid 2px;
  text-decoration: none;
  color: #ffffff;
  white-space: nowrap;
  z-index: 0;
  width: 100%;
  height: 100%;
  line-height: 27px;
  /* border-radius: 5px; */
  border-image: linear-gradient(#f80, #ff778d) 2 2 2;
  /* border-radius: 5px; */
  line-height: 30px;
}

.hbtn.hb-fill-middle2:before {
  position: absolute;
  content: "";
  /* background: #ffffff; */
  background-image: -webkit-gradient(
    linear,
    left top,
    right top,
    from(#ff778d),
    to(#ff5fa4)
  );
  transition-duration: 0.3s;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: auto;
  left: auto;
  width: 0;
  height: 100%;
  opacity: 1;
}

.hbtn.hb-fill-middle2:hover:before {
  width: 50%;
  height: 100%;
  opacity: 1;
}

.hb-fill-middle2 {
  background: transparent;
  color: #ffffff;
  transition: color 0.3s ease, background 0s ease;
}

.hb-fill-middle2:after {
  position: absolute;
  content: "";
  background-image: -webkit-gradient(
    linear,
    right top,
    left top,
    from(#ff778d),
    to(#ffa065)
  );
  transition-duration: 0.3s;
  z-index: -1;
  top: 0;
  right: auto;
  bottom: auto;
  left: 0;
  width: 0;
  height: 100%;
  opacity: 1;
}

.hb-fill-middle2:hover:after {
  width: 50%;
}
.login_a {
  display: inline-block;
  /* width: 100px; */
  height: 40px;
  line-height: 35px;
  color: #fff;
  text-align: center;
}
.login_a:hover {
  cursor: pointer;
}
.login_div {
  width: 24vw;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
}
/* 导航栏相关 */
.hidden_title_ul_first_li {
  font-size: 1.5rem;
  color: #e9c06c;
  margin-top: 10px;
  /* text-align: center; */
  /* font-weight: 600; */
  padding-left: 20%;
}
.hidden_title_ul_li {
  font-size: 1.4rem;
  /* color: #fff; */
  margin-top: 10px;
  /* text-align: center; */
  padding-left: 12px;
  padding-left: 30%;
}
.big-title {
  font-size: 1.8rem;
  margin-left: 10px;
  font-weight: bold;
}
.menu-list {
  height: 85vh;
  overflow-y: auto;
}
</style>
<style>
#phone-nav .drawer .el-drawer__header span {
  font-size: 2rem;
  font-weight: bold;
}
/* #phone-nav .drawer .el-drawer__header :focus {
  outline: 0;
}
#phone-nav .el-drawer :focus {
  outline: 0;
} */
/* 去掉抽屉的边框 */
#phone-nav :focus {
  outline: 0;
}
</style>


